import { _decorator, Component, Node, director, BoxCollider, Label, Director, AudioSource } from 'cc';
const { ccclass, property } = _decorator;
import { audioplayer } from './audioplayer';

@ccclass('ManagerGame')
export class ManagerGame extends Component {
    @property(Node)
    public gameover : Node = null;
    public GO: boolean = false;
    @property ([Node])
    public listaSpawns : Node [] = [];
    private puntos : number = 0;
    @property(Label)
    public score : Label = null;
    @property(Label)
    public FinalScore : Label = null;
    @property([Node])
    public ListaNodos: Node [] = [];
    @property(AudioSource)
    public audio : AudioSource = null;




    protected onLoad(): void {
        for(let i = 0; i < this.ListaNodos.length; i++){
            this.ListaNodos[i].active=false;
        }
        this.gameover.active=false;
        //director.pause();
        this.scheduleOnce (function(){
            this.ListaNodos[0].active=true;
        },.2)
        this.scheduleOnce(function(){
            this.ListaNodos[1].active=true;
        },.5)
        this.scheduleOnce(function(){
            this.ListaNodos[2].active=true;
            this.audio.play();
        },.8)
    }
    
    start() {
        this.schedule(function(){
            this.sumapuntos(10);

        },20)
        

    }
    sumapuntos(suma:number){
        this.puntos += suma;
        this.score.string = this.puntos.toString();
        this.audio.play();
    }
    slowTime(){
        //director.getScheduler().setTimeScale(.5);
        this.scheduleOnce(function(){
            for(let i = 0 ; i < this.listaSpawns.length; i++)
                {
                    this.listaSpawns[i].active = false;
                }
                this.audio.getComponent(AudioSource).clip = this.audio.getComponent(audioplayer).ListaAudio[2];
                this.audio.play();
            this.gameover.active=true;
            this.FinalScore.string=this.score.string;
        },1)
    }

    update(deltaTime: number) {

        if(this.GO)
            {
                this.GO=false;
                this.slowTime();
            }
        
    }
}


