import { _decorator, Component, instantiate, Node, Prefab, randomRangeInt } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('SpawnMonedas')
export class SpawnMonedas extends Component {

    @property([Node])
    public listamonedas : Node [] = [] ;
    
    private creando : boolean = false;
    private Timer : number = 2;
    @property(Prefab)
    public prefab : Prefab = null
    private index : number = 0;
    protected onLoad(): void {
        
    }
    start() {

    }
    spawnMonedas(){
        this.index = randomRangeInt(0,4)
        switch(this.index){
            case 0: 
                    const node = instantiate(this.prefab)
                    node.setParent(this.node);
                    node.setPosition(this.listamonedas[0].position);
            break;
            case 1: const node1 = instantiate(this.prefab);
                    node1.setParent(this.node);
                    node1.setPosition(this.listamonedas[1].position);
                    break;
            case 2: const node2 = instantiate(this.prefab);
                    node2.setParent(this.node);
                    node2.setPosition(this.listamonedas[2].position);
                    break;
            case 3: 
            const node3 = instantiate(this.prefab);
                    node3.setParent(this.node);
                    node3.setPosition(this.listamonedas[3].position);
            break;
            case 4: 
                    const node4 = instantiate(this.prefab);
                    node4.setParent(this.node);
                    node4.setPosition(this.listamonedas[4].position);
            break;

        }
        this.creando=false;

    }
    update(deltaTime: number) {
        if(!this.creando){
            this.creando=true;
            this.scheduleOnce(function(){
                this.spawnMonedas();
            }, this.Timer);
        }
        
    }
}


