import { _decorator, Component, Node, Input, EventTouch, Vec3, NodeEventType, KeyCode, input, AnimationClip, Animation, CCBoolean } from 'cc';
import { InputManager } from './InputManager';
//import { Animaciones } from './Animaciones';

const { ccclass, property } = _decorator;

@ccclass('MovimientoCubeta')
export class MovimientoCubeta extends Component {
  

    @property public limiteIzq:number = -300;
    @property public limiteDer:number = 300;
    @property(InputManager) public inputManager: InputManager;
    @property(Node) nodeImage: Node=null;
    public posY:Vec3;

    @property
    public speed: number = 500;
    @property(Vec3)
    public targetPosition: Vec3 = new Vec3();
    @property(Vec3)
    public moveDirection: Vec3 = new Vec3();
    @property(Vec3)
    public newPosition: Vec3 = new Vec3();

    @property public flechas:boolean=false;
    @property public touch:  boolean = false;
    @property(Vec3) public transf: Vec3 = null;

    private derecha:boolean;
    private izquierda:boolean;
    public YaEntre: boolean;

    @property public state: string = "otro";
    private prevMouseX: number = 0;
    private direction: number = 0;
    @property(CCBoolean)
    public choque: boolean=false;
   

    onLoad(){
        this.choque = false;
        this.derecha = false;
        this.izquierda = false;
        this.YaEntre=false;
        //this.anim= this.getComponent(Animaciones);
       // this.animation = this.node.getComponentInChildren(Animation)
        //this.anim.IDLE();
        this.posY=this.nodeImage.getWorldPosition();

       
        input.on(Input.EventType.KEY_DOWN, this.Teclas,this);
        console.log("cargando flechas")
       
        
        this.nodeImage.on(Input.EventType.TOUCH_START,this.Touch,this)

        this.nodeImage.on(Input.EventType.TOUCH_END,this.Stop,this)
        this.nodeImage.on(Input.EventType.TOUCH_CANCEL,this.Stop,this)
        /*this.nodeImage.on(Node.EventType.TOUCH_MOVE,(event:EventTouch)=>{
            let loc=event.getLocation();
            
            this.nodeImage.setWorldPosition(new Vec3(loc.x, this.posY.y, 0));
            
        })

        this.nodeImage.on(Node.EventType.TOUCH_END,(event:EventTouch)=>{

        })

        this.nodeImage.on(Node.EventType.TOUCH_CANCEL,(event:EventTouch)=>{
            
        })*/

       
       
    }

    onDestroy() {
        // Desregistrar eventos al destruir el componente
        this.node.off(Node.EventType.TOUCH_MOVE, this.Touch, this);
        

    }

    Stop()
    {
        this.YaEntre = false;
        this.state="idle";
        console.log("regresa idle");
        
        this.suelta();
    }
    Detener()
    {
        if(this.nodeImage.position.x<=this.limiteIzq)
        {
            this.nodeImage.setPosition(new Vec3(this.limiteIzq,-180,0));
        }

        if(this.nodeImage.position.x >=this.limiteDer)
        {
            this.nodeImage.setPosition(new Vec3(this.limiteDer,-180,0));
        }

       //this.anim.IDLE();
    }
    Touch()
    {
            this.nodeImage.on(Node.EventType.TOUCH_MOVE,(event:EventTouch)=>{
                this.touch=true;
                let loc=event.getUILocation();
    
                // Determinar la direcciÃ³n del movimiento del mouse
                const currentMouseX = event.getLocationX();
                if (currentMouseX > this.prevMouseX) {
                    this.direction = 1; // Movimiento hacia la derecha
                    this.izquierda=false;
                } else if (currentMouseX < this.prevMouseX) {
                    this.direction = -1; // Movimiento hacia la izquierda
                    this.derecha=false;
    
                }
                this.nodeImage.setWorldPosition(new Vec3(loc.x, this.posY.y, 0));
                this.prevMouseX = currentMouseX;
            })

       
        
    }

    NoTeclas()
    {
        this.flechas=false;
    }
    Teclas()
    {
        console.log("flechas")
        this.flechas=true;
        
    }
    cambioMove(){
     if(this.touch){
        
                this.flechas = false;
        }
     if(InputManager.isKeyPressed(KeyCode.ARROW_LEFT)||InputManager.isKeyPressed(KeyCode.ARROW_RIGHT) ){
    
                this.flechas=true;
                this.touch=false;
        }
    
    }
    suelta(){
        this.transf=this.nodeImage.getPosition();
        this.targetPosition=this.transf;
        this.nodeImage.getPosition(this.transf);

       
    }
    mueve(deltaTime){
        this.cambioMove();
        if(this.flechas){
            
            //console.log("flechas");
            this.moveDirection.x = 0
            this.moveDirection.y = 0
            if(InputManager.isKeyPressed(KeyCode.ARROW_LEFT))
            {
             this.moveDirection.x -= 1          
             
            }
            if(InputManager.isKeyPressed(KeyCode.ARROW_RIGHT)){
            this.moveDirection.x += 1           
            }

            if(!InputManager.isKeyPressed(KeyCode.ARROW_RIGHT)&&!InputManager.isKeyPressed(KeyCode.ARROW_LEFT))
            {
                this.YaEntre = false;
                
            }

            ///*
            //OPTIM
            Vec3.normalize(this.moveDirection, (new Vec3 (this.moveDirection.x, 0,0)));
            Vec3.scaleAndAdd(this.targetPosition, this.targetPosition, this.moveDirection, this.speed * deltaTime)
            const lerpFactor = 1 - Math.pow(0.001, deltaTime);
            Vec3.lerp(this.newPosition, new Vec3(this.nodeImage.position.x,-180,0), (new Vec3(this.targetPosition.x, -180, 0 )), lerpFactor);
            this.nodeImage.setPosition(this.newPosition);
        }
        else{
            
        }      
        this.Detener();
    }

   
    update(deltaTime:number)
    {
        if(!this.choque)
            {
                this.mueve(deltaTime);
            }
        
    }
}


