import { _decorator, CCString, Component, instantiate, Node, Prefab, randomRangeInt, Sprite, Vec3, Color, BoxCollider2D } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('SpawnCoches')
export class SpawnCoches extends Component {
    @property([Prefab])
    public listaprefab: Prefab [] = [];
    @property([Vec3])
    public listaposiciones :  Vec3 [] = [];
    @property(CCString)
    private nombre : string = "";
    @property(Node)
    public player : Node = null;

    public deten: boolean =false;
   
    private timer :  number = 3;
    private time : number = 0;
    private creando : boolean = false;
    private index : number = 0;
    protected onLoad(): void {
        this.nombre = this.player.getComponent(Sprite).spriteFrame.name.toString();
        this.schedule(function () {
            this.timer -= 0.2;
            if (this.timer <= 0.6) {
                this.timer = 0.6;
                
            }
        },2)
    }
    start() {

    }
    cochesOpacidad(){
        for(let i = 4; i < this.node.children.length; i++){
            console.log("coloress" + this.node.children.length);
            let colorS = this.node.children[i].getComponent(Sprite).color;
            console.log(colorS);
            this.node.children[i].getComponent(BoxCollider2D).group= 128;
            this.node.children[i].getComponent(Sprite).color = new Color( colorS.r, colorS.g, colorS.b, 60);
        }
    }
    timerbaja(){
            this.timer -= .2;
            if(this.timer >= .3)
                {
                    this.timer = .3;
                }
    }
    Spawn(){
        this.index=randomRangeInt(0,4);
        this.creando=false;

        switch(this.index){
            case 0: 
                    let index= randomRangeInt(0,11);
                    const temp = instantiate(this.listaprefab[index]);
                    if(temp.getComponent(Sprite).spriteFrame.name== this.nombre){
                        index +=1;
                        if(index+1 > 11){
                            index = 0;
                        }

                        const node0 = instantiate(this.listaprefab[index]);
                        node0.setParent(this.node);
                        node0.setPosition(this.listaposiciones[0]);
                    }
                    else{
                        const node0 = instantiate(this.listaprefab[index]);
                        node0.setParent(this.node);
                        node0.setPosition(this.listaposiciones[0]);
                    }
                    temp.destroy();
                   
            
            break;
            case 1: 
                    let index1= randomRangeInt(0,11);
                    const temp1 =instantiate(this.listaprefab[index1]);
                    if(temp1.getComponent(Sprite).spriteFrame.name== this.nombre){
                        index1 +=1;
                        if(index1+1 > 11){
                            index1 = 0;
                        }

                        const node1 = instantiate(this.listaprefab[index1]);
                        node1.setParent(this.node);
                        node1.setPosition(this.listaposiciones[1]);
                    }
                    else{
                        const node1 = instantiate(this.listaprefab[index1]);
                        node1.setParent(this.node);
                        node1.setPosition(this.listaposiciones[1]);
                    }
                    temp1.destroy();
            
            
            break;
            case 2: 
                    let index2= randomRangeInt(0,11);
                    const temp2 =instantiate(this.listaprefab[index2]);
                    if(temp2.getComponent(Sprite).spriteFrame.name== this.nombre){
                        index2 +=1;
                        if(index2+1 > 11){
                            index2 = 0;
                        }
                        const node2 = instantiate(this.listaprefab[index2]);
                        node2.setParent(this.node);
                        node2.setPosition(this.listaposiciones[2]);
                    }
                    else{

                        const node2 = instantiate(this.listaprefab[index2]);
                        node2.setParent(this.node);
                        node2.setPosition(this.listaposiciones[2]);
                    }
                    temp2.destroy();
            
            
            break;
            case 3: 
            let index3= randomRangeInt(0,11);
            const temp3 =instantiate(this.listaprefab[index3]);
            if(temp3.getComponent(Sprite).spriteFrame.name == this.nombre){
                index3 +=1;
                if(index3+1 > 11){
                    index3 = 0;
                }
                const node3 = instantiate(this.listaprefab[index3]);
                node3.setParent(this.node);
                node3.setPosition(this.listaposiciones[3]);
            }
            else{

                const node3 = instantiate(this.listaprefab[index3]);
                node3.setParent(this.node);
                node3.setPosition(this.listaposiciones[3]);
            }
            temp3.destroy();
            
            break;
            case 4: 
            
            break;
        }


    }
    reanuda(){
        this.time+=0.005;
        if(this.time > 1)
            {
                this.time =0;
                this.deten=false;
            }
        
    }


    update(deltaTime: number) {
        
        if(this.deten){
            
            this.reanuda();
        }
        else{
            if(!this.creando){
                this.creando=true;
                this.scheduleOnce(function(){
                    this.Spawn()
                }, this.timer)
            }

        }

        
        
    }
}


