import { _decorator, CCFloat, Component, instantiate, Node, Prefab, randomRangeInt, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ArbolSpawn')
export class ArbolSpawn extends Component {
    @property(Vec3)
    public posicionDerecha: Vec3 = new Vec3;
    @property(Vec3)
    public posicionIzquieda: Vec3 = new Vec3;
    @property(Prefab)
    public arbol : Prefab = null;
    @property(CCFloat)
    private timer: number = 1;

    private creando : boolean = false;
    private index : number = 0;
    start() {
       

    }

    spawnarbol(){

        this.index = randomRangeInt(0,4);
        switch(this.index){
            case 0:
                const node1 = instantiate(this.arbol);
                node1.position = this.posicionIzquieda;
                node1.setParent(this.node)
                break;
            case 1:  
            
            break;
            case 2:
                const node2 = instantiate(this.arbol);
                node2.position = this.posicionIzquieda;
                node2.setParent(this.node);

                const node22 = instantiate(this.arbol);
                node22.position = this.posicionDerecha;
                node22.setParent(this.node)

                break;

            case 3: break;
            case 4: 
            const node4 = instantiate(this.arbol);
                node4.position = this.posicionDerecha;
                node4.setParent(this.node)
            break;

           

        }
        this.creando=false;
    }

    update(deltaTime: number) {

        if(!this.creando){
            this.creando=true;
            this.scheduleOnce(function(){

                this.spawnarbol()
            },this.timer)
        }
        
    }
}


