import { _decorator, Component, instantiate, Node, Prefab, randomRangeInt } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('SpawnPostes')
export class SpawnPostes extends Component {
    @property([Prefab])
    public listapostes: Prefab[] = [];
    private timer: number = 10;
    private creando: boolean = false;
    private index : number = 0;

    start() {

    }
    spawnPostes(){
        this.index = randomRangeInt(0,4)
        switch(this.index){
            case 0: const node0 = instantiate(this.listapostes[0])
                    node0.setParent(this.node);
                    node0.setPosition(20,0,0);
             break;
            case 1: const node1 = instantiate(this.listapostes[1])
                    node1.setParent(this.node);
                    node1.setPosition(20,0,0);
            break;
            case 2: const node2 = instantiate(this.listapostes[2])
                    node2.setParent(this.node);
                    node2.setPosition(20,0,0);
             break;
            case 3: const node3 = instantiate(this.listapostes[3])
                    node3.setParent(this.node);
                    node3.setPosition(20,0,0);
            break;
            case 4: const node4 = instantiate(this.listapostes[4])
                node4.setParent(this.node);
                node4.setPosition(20,0,0);
            break;
        }
        this.creando=false;
    }

    update(deltaTime: number) {
        if(!this.creando)
            {
                this.creando=true;
                this.scheduleOnce(function(){
                    this.spawnPostes()
                },this.timer)
            }
        
    }
}


