import { _decorator, Component, Node, Collider2D, Contact2DType, IPhysics2DContact, BoxCollider2D, RigidBody2D, UITransform, Sprite, AudioSource, SpriteFrame, color, Color } from 'cc';
import { ManagerGame } from './ManagerGame';
import { MovimientoCubeta } from './MovimientoCubeta';
import { moneda } from './moneda';
import { audioplayer } from './audioplayer';
import { SpawnCoches } from './SpawnCoches';
const { ccclass, property } = _decorator;

@ccclass('PlayerCollision')
export class PlayerCollision extends Component {

    @property(Node)
    public gamemanager: Node = null;
    @property (Node)
    public spawncoches : Node = null;
    protected onLoad(): void {
        let collider=this.node.getComponent(Collider2D)

        if(collider)
        {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onContact,this)
        }
    }
    start() {

    }
    onContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){
        console.log(otherCollider.group)
        if(otherCollider.group == 4)
            {
                this.node.getComponent(AudioSource).clip = this.node.getComponent(audioplayer).ListaAudio[1];
                this.node.getComponent(AudioSource).play();
                this.node.getComponent(MovimientoCubeta).Stop();
                this.node.getComponent(MovimientoCubeta).choque = true;
                this.node.getComponent(MovimientoCubeta).enabled=false;
                
                otherCollider.getComponent(BoxCollider2D).density = .2;
                otherCollider.getComponent(RigidBody2D).gravityScale = .2;
                otherCollider.getComponent(BoxCollider2D).enabled=false;
                this.gamemanager.getComponent(ManagerGame).GO = true;
            }

        if(otherCollider.group == 64){

            otherCollider.node.getComponent(Sprite).enabled=false;
            //this.node.getComponent(AudioSource).clip = this.node.getComponent(audioplayer).ListaAudio[0];
            this.spawncoches.getComponent(SpawnCoches).deten=true;
            this.spawncoches.getComponent(SpawnCoches).cochesOpacidad();
           
1
            this.gamemanager.getComponent(ManagerGame).sumapuntos(otherCollider.node.getComponent(moneda).valor);
            
            this.scheduleOnce(function(){

                otherCollider.node.destroy();


            },.1)
        }
    }

    update(deltaTime: number) {
        
    }
}


